<template>
  <section class="time-picker">
    <el-time-select
        v-model="val"
        :picker-options="{
            start: '00:00',
            step: '00:15',
            end: '23:59'
        }"
        placeholder="Select Time"
        @change="setVal" 
    ></el-time-select>
  </section>
</template>

<script>
export default {
  data() {
    return {
      val: null
    };
  },
  methods: {
    setVal() {
      this.$emit("input", this.val);
    }
  }
};
</script>

