import axios from 'axios'
axios.defaults.withCredentials = true

export const boardService = {

    query,
    getById,
    remove,
    save,
    getEmptyBoard
}

const BASE_URL = ( process.env.NODE_ENV !== 'development') ? '/api/board' : '//localhost:3000/api/board' ;

function _getURL(id=''){
	return `${BASE_URL}/${id}`
}

function query(filterBy) {
    return axios.get(`${_getURL()}?name=${filterBy.searchStr}&type=${filterBy.type}&inStock=${filterBy.isInStock}&sort=${filterBy.sort}`)
        .then(res => res.data)
}

function getById(id) {
    return axios.get(`${_getURL(id)}`)
        .then(res => res.data)    
}

function remove(id) {
    return axios.delete(`${_getURL(id)}`)
}

function save(board) {
    return (board._id) ? _update(board) : _add(board)
}

function getEmptyBoard() {
    return {
        name: '',
        price: null,
        type: null,
        createdAt: Date.now(),
        inStock: false,
    }
}

function _update(board) {
    return axios.put(`${_getURL(board._id)}`, board)
                .then(res =>res.data)
}

function _add(board) {
    return axios.post(`${_getURL()}`, board)
        .then(res =>res.data)
}
