import Vue from 'vue'
import VueRouter from 'vue-router'
import boardApp from '../views/board-app.vue'
import boardEdit from '../views/board-edit.cmp.vue'
import boardDetails from '../views/board-details.cmp'
import boardBranch from '../views/board-branch.cmp'
import userProfile from '../views/user-profile.cmp'
import userLogin from '../views/user-login.cmp'
import userSignup from '../views/user-signup.cmp'

Vue.use(VueRouter)

  const routes = [
  {
    path: '/board/details/:boardId',
    name: 'board-details',
    component: boardDetails
  },
  {
    path: '/board/edit/:boardId?',
    name: 'board-edit',
    component: boardEdit
  },
  {
    path: '/',
    name: 'board-app',
    component: boardApp
  },
  {
    path: '/stores',
    name: 'board-branch',
    component: boardBranch
  },
  {
    path: '/user',
    name: 'user-profile',
    component: userProfile
  },
  {
    path: '/login',
    name: 'user-login',
    component: userLogin
  },
  {
    path: '/signup',
    name: 'user-signup',
    component: userSignup
  },

]

const router = new VueRouter({
  routes
})

export default router
