<template>
    <section class="chat-app">
        <el-input placeholder="Write Us" v-model="inputVal">
        <el-button @click="sendValue" slot="append">Send</el-button>
        </el-input>
    </section>
</template>

<script>
export default {
    // props: ['value'],
    data(){
        return{
            inputVal:''
        }
    },
    methods: {
        sendValue(){
            this.$emit('inputAdded', this.inputVal)
            this.inputVal=''
        }
    }
}
</script>

