<template>
  <section class="board-branch">
    <h1>Our stores</h1>

    <GmapMap
      :center="center"
      :zoom="7"
      map-type-id="terrain"
      style="width: 500px; height: 300px"
    >
      <GmapMarker
        :key="index"
        v-for="(m, index) in markers"
        :position="m.position"
        :clickable="true"
        :draggable="true"
        @click="center=m.position"
      />
    </GmapMap>
    <button @click="centerMap(32.085300, 34.781769)">Tel Aviv</button>
    <button @click="centerMap(40.712776, -74.005974)">New York</button>
    <button @click="centerMap(51.507351, -0.127758)">London</button>
    <pop-up v-if="popUpShown">
      <h1 slot="header">Contact Us Now</h1>
      <h1 slot="footer">Start A Chat</h1>
    </pop-up>
    <button class="chat" @keyup.esc="togglePopUp('close')" @click="togglePopUp">Chat</button>
  </section>
</template>

<script>
import popUp from '../components/pop-up.cmp'
export default {
  data() {
    return {
      popUpShown: false,
      markers: [
        { position: { lat: 40.712776, lng: -74.005974 } },
        { position: { lat: 32.085300, lng: 34.781769 } },
        { position: { lat: 51.507351, lng: -0.127758 } }
      ],
      center:{lat: 32.085300, lng: 34.781769}
    };
  },
  methods: {
      togglePopUp(state='toggle'){
        if (state==='close') return this.popUpShown=false
        this.popUpShown=!this.popUpShown
      },
      centerMap(lat,lng){
          this.center = { lat: lat, lng: lng }
      }
  },
  watch: {
    
  },
  components: {
    popUp
  }
};
</script>

<style scoped>
  button.chat{
    position: fixed;
    right: 10px;
    bottom: 10px;
  }
</style>