<template>
    <section class="user-login">
      <h1>Login:</h1>
      <div v-if="loggedinUser">
          <h3>Currently logged as: {{loggedinUser.username}}</h3>
          <button @click="logout">Logout</button>
      </div>
      <form v-else @submit.prevent="login">
        <el-input placeholder="Please input Email" v-model="credentials.email"></el-input>
        <el-input placeholder="Please input user name" v-model="credentials.username"></el-input>
        <el-input placeholder="Please input password" v-model="credentials.password" show-password></el-input>
        <button>Login</button>
      </form>
      <p v-if="!loggedinUser">Not registered yet? <button @click="signup">Sign Up!</button></p>
     
    </section>
</template>
<script>
import {userStore} from '../store/user.store.js'
export default {
  data(){
    return {
      loggedinUser: '',
      credentials: {
        username: '',
        password: '',
        email: '',
      },
    }
  },
  methods: {
    async login() {
      let user = await this.$store.dispatch({type: 'login', userCred: this.credentials})
      console.log('loggedin user:', user)
      this.loadLoggedinUser()
      // this.$router.push('/')
      // userService.login(this.credentials)
      //   .then(loggedinUser => {
      //       console.log('LOGGED IN!', loggedinUser);
      //       this.$router.push('/')
      //   })
      //   .catch(err=>console.log('THIS ERR:', err))
    },
    async logout(){
      let user = await this.$store.dispatch({type: 'logout'})
      console.log('loggedout user:', user);
      this.loadLoggedinUser()
    },
    signup(){
        this.$router.push('/signup')
    },
    loadLoggedinUser(){
      this.loggedinUser = this.$store.getters.loggedinUser
    }
  },
  created() {
    this.loadLoggedinUser()
  },

}
</script>

