<template>
  <div  v-if="boardToShow" class="board-details">
    <h1>Board Details:</h1>
    <p>ID: {{boardToShow._id}}</P>
    <p>Name: {{boardToShow.name}}</P>
    <p>Price: {{boardToShow.price | formatNum}}</P>
    <p>Type: {{boardToShow.type}}</P>
    <p>Created At: {{boardToShow.createdAt | formatTime}}</P>
    <p>Is In Stock: {{boardToShow.inStock}}</P>
    <img v-if="boardToShow.imgUrl" :src="boardToShow.imgUrl"/>
  </div>
</template>

<script>

import {boardService} from '../services/board.service.js'

export default {
    data(){
        return {
            boardToShow: null
        }
    },
    components: {

    },
        methods: {
        loadBoard(){
            let {boardId} = this.$route.params;
            boardService.getById(boardId)
                .then(board=> this.boardToShow = board)
        }
    },
    created() {
            this.loadBoard()
            
    }
}
</script>

