<template>
    <section class="user-profile">
        <h1>user profile</h1>
        <form @submit.prevent="savePrefs">
        <select-cmp v-model="userPrefs.gender" :values="selectVals" choise="Gender"></select-cmp>
        <color-picker v-model="userPrefs.color"></color-picker>
        <time-picker v-model="userPrefs.time"></time-picker>
        <button>Save</button>
        </form>
        <pre>{{userPrefs}}</pre>
    </section>
</template>

<script>
import selectCmp from '../components/select.cmp'
import colorPicker from '../components/color-picker.cmp'
import timePicker from '../components/time-picker.cmp'
export default {
    data(){
        return {
            userPrefs:{
                gender: null,
                color: null,
                time: null
            },
            selectVals: ['Male', 'Female', 'Id rather not say']
        }
    },
    methods: {
        savePrefs(){
            this.$store.commit({type: 'setUserPrefs', userPrefs: this.userPrefs})
            this.$router.push('/')
        }
    },
    created(){
        console.log(this.$store.getters.userPrefs);
        
        this.userPrefs = JSON.parse(JSON.stringify(this.$store.getters.userPrefs));
    },
    components: {
        selectCmp,
        colorPicker,
        timePicker
    }
}
</script>

