<template>
  <section class="board-edit"  v-if="boardToEdit">
        <h1> {{ (boardToEdit._id) ? 'Edit Board' : 'Add Board' }} </h1>
        <form @submit.prevent="saveBoard">
            <label>Name:<el-input placeholder="Board Name" v-model="boardToEdit.name"></el-input></label>
            <label>Category:<el-select v-model="boardToEdit.type" placeholder="Choose Category">
                <el-option value="Funny">Funny</el-option>
                <el-option value="Educational">Educational</el-option>
                <el-option value="Adult">Adult</el-option>
            </el-select></label>
            <label>Price:<el-input-number v-model="boardToEdit.price" controls-position="right" :min="0" placeholder="Price"></el-input-number></label>
            <label>In Stock:<el-checkbox v-model="boardToEdit.inStock" :label="(boardToEdit.inStock) ? 'Yes' : 'No '" border></el-checkbox></label>
            <!-- <label>Image:<el-input placeholder="Image URL" type="file" v-model="boardToEdit.name"></el-input></label> -->
            <input type="file" @change="onUploadImg">
            <!-- <label>Name:<input type="text" v-model="boardToEdit.name"/></label> -->
            <!-- <label>Is In Stock:<input type="checkbox" v-model="boardToEdit.inStock"/></label> -->
            <!-- <label>Price:<input type="number" v-model="boardToEdit.price"/></label> -->
            <!-- <label>Type:
                <select v-model="boardToEdit.type">
                    <option value = "Funny" >Funny</option>
                    <option value = "Adult" >Adult</option>
                    <option value = "Educational" >Educational</option>
                </select>
            </label> -->
            <button>Save</button>
            <pre>{{boardToEdit}}</pre>
        </form>
        <button v-if="boardToEdit._id" @click="removeBoard">Delete Board</button>
  </section>
</template>

<script>
    // "_id": 29165,
    // "createdAt": "1994-11-15T03:01:09.526Z",

import {boardService} from '../services/board.service.js'
import {uploadImg} from '../services/imgUpload.service'

export default {
    data(){
        return {
            boardToEdit: null,
        }
    },
    computed: {
        boards() {
            return this.$store.getters.boards;
       },
    },
    components: {

    },
    methods: {
        loadBoard(){
            let {boardId} = this.$route.params; 
            if (boardId){
                boardService.getById(boardId)
                    .then(board=>{
                        this.boardToEdit = JSON.parse(JSON.stringify(board));
                    })
            }else{
                    this.boardToEdit = boardService.getEmptyBoard()
            }
        },
        saveBoard(){
            if (!this.boardToEdit.name) return;
            this.$store.dispatch({type: 'saveBoard', board: this.boardToEdit})
                .then((savedBoard)=>{
                    this.$router.push('/');
                    this.loadBoard();
                })
            
        },
        removeBoard(){
            this.$store.dispatch({type: 'removeBoard', id: this.boardToEdit._id})
            .then((savedBoard)=>{
                this.$router.push('/');
                this.loadBoard();
            })
        },
        onUploadImg(ev){
            uploadImg(ev)
            .then(res=> this.boardToEdit.imgUrl = res.url)
            console.log('666', this.boardToEdit);
            
            //             !
        }
    },
    created() {
        this.loadBoard()     
    },
    watch: {
        '$route.params.boardId'() {   
            this.loadBoard()
        }
    }
}
</script>

