<template>
  <div class="board-app">
    <h1>Board App</h1>
    <board-filter @setFilter="setFilter"></board-filter>
    <board-list :boards="boards"></board-list>
  </div>
</template>

<script>
import boardList from '../components/board-list.cmp.vue';
import boardFilter from '../components/board-filter.cmp.vue';

export default {
    data(){
        return {

        }
    },
    computed: {
        boards() {
            return this.$store.getters.boards;
       },
    },
    methods: {
        setFilter(filterBy){
            this.$store.commit({type: 'updateFilterBy', filter: filterBy})
            this.$store.dispatch({type: 'loadBoards'})
        }, 
    },
    components: {
        boardList,
        boardFilter
    },
        created(){
        this.$store.dispatch({type: 'loadBoards'})
    },
}
</script>

