<template>
    <section class="pop-up">
        <slot name="header"></slot>
        <slot><div class="conversation"></div></slot>
        <slot name="footer"></slot>

        <chat-app @inputAdded="inputAdded"></chat-app>
    </section>
</template>

<script>
import chatApp from './chat-app.cmp'
export default {
    data(){
        return{
        }
    },
    methods:{
        inputAdded(input){
            this.$el.querySelector('.conversation').innerHTML += `<p>You: ${input}<p>`
            setTimeout(()=>{
                this.$el.querySelector('.conversation').innerHTML += `<p>Support: We will do our best!<p>`
            },1000)
            console.log(this.$el);
            // this.conversation += 
        }
    },
    components: {
        chatApp
    }
}
</script>

<style scoped>
    .pop-up{
        position: fixed;
        bottom: 30px;
        right: 10px;
        background-color: rgb(191, 204, 238);
        padding: 10px 20px 80px;
        height: 400px;
        width: 300px;
        overflow: auto;
    }
    h1:nth-of-type(2){
        position: fixed;
        bottom: 60px;
        background-color: rgb(191, 204, 238);
    }
    .chat-app{
        position: fixed;
        bottom: 30px;
        right: 50px;
        /* width: 100%; */
    }
    .conversation{
        object-fit: fill;
    }
</style>>

