<template>
<section class="color-picker">
    <label>Choose Color
    <el-color-picker v-model="val" @input="setVal">Choose Color</el-color-picker>
    </label>
</section>
 
</template>

<script>
export default {
    data(){
        return{
            val: null
        }
    },
    methods: {
        setVal(){
            this.$emit('input', this.val)
        }
    }

}
</script>

