<template>
  <el-select v-model="val" @change="setVal" :placeholder="choise">
      <el-option :value="values[0]">{{values[0]}}</el-option>
      <el-option :value="values[1]">{{values[1]}}</el-option>
      <el-option :value="values[2]">{{values[2]}}</el-option>
    </el-select>
</template>

<script>
export default {
    props: ['values', 'choise'],
    data(){
        return{
            val: null
        }
    },
    methods: {
        setVal(){
            this.$emit('input', this.val)
        }
    }

}
</script>

